@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
html {
  position: relative;
  min-height: 100%; }

body {
  font-family: "Roboto", sans-serif;
  margin-bottom: 60px;
  /* Margin bottom by footer height */ }
  body header {
    padding-top: 10px;
    padding-bottom: 10px; }
  body .card {
    margin: 3px; }
  body footer {
    padding-top: 60px; }


.Main .card {
  position: relative;
  margin: 30px 0;
  padding: 30px 0;
  color: white;
  /* animation */
  -webkit-animation-name: main-card-animation;
          animation-name: main-card-animation;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }

.Main .service {
  font-size: 2.5rem; }

@-webkit-keyframes main-card-animation {
  0% {
    background-color: white; }
  25% {
    background-color: #16a085; }
  100% {
    background-color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15); } }

@keyframes main-card-animation {
  0% {
    background-color: white; }
  25% {
    background-color: #16a085; }
  100% {
    background-color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15); } }

.AboutTeam .avatar {
  vertical-align: middle;
  width: 320px;
  height: 320px;
  border-radius: 50%; }

.AboutTeam .avatar.size-1 {
  width: 40px;
  height: 40px; }

.AboutTeam .avatar.size-2 {
  width: 70px;
  height: 70px; }

.AboutTeam .avatar.size-3 {
  width: 90px;
  height: 90px; }

.AboutTeam .avatar.size-4 {
  width: 110px;
  height: 110px; }

.SearchForm .input-group {
  height: 3rem;
  margin-bottom: 1rem; }
  .SearchForm .input-group .input {
    width: 100%;
    padding: 0 1rem;
    border: 2px #3DC829 solid; }

.Header .icon {
  padding: 0 3px; }

.Brand {
  padding: 0; }
  .Brand .icon {
    padding: 0.65rem;
    border-radius: 20%;
    background-color: #fffa65;
    border: 2px solid #05c46b; }
  .Brand .title {
    color: #576574;
    font-size: 2rem;
    padding: 0 1rem; }


